<template>
  <div class="wraper">
    <NavBar></NavBar>
    <div class="content friends">
      <p class="title">:</p>
      <div class="f-list">
        <div
          class="f-item"
          v-for="item in lists">
          <a :href="item.url"   target="_blank" class="avatar" :style="{'background-image': 'url('+ item.avatar + ')'}"></a>
          <span class="line"></span>
          <div class="des">
            <p class="name">{{ item.name }}</p>
            <p>{{ item.des }}</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import NavBar from '../components/Nav'
export default {
  data () {
    return {
      lists: [
        {
          name: '',
          url: 'http://funnycoder.lofter.com',
          des: 'Coder~',
          avatar: 'http://imgsize.ph.126.net/?imgurl=http://imglf2.ph.126.net/80tgwfppFxSJbTboaJ4R2Q==/6631977061815991766.jpg_96x96x0x90.jpg'
        },
        {
          name: '',
          url: 'http://zhaoyuxiang.cn',
          des: ', ~',
          avatar: 'https://ooo.0o0.ooo/2016/12/20/5858bbde6e8ac.jpg'
        },
        {
          name: '',
          url: 'https://blog.wanan.me',
          des: '~ ',
          avatar: 'https://avatars3.githubusercontent.com/u/7876498'
        },
        {
          name: '',
          url: 'http://noder.club',
          des: 'node',
          avatar: 'https://ws3.sinaimg.cn/large/7d051389gw1fbdeilrksgj202s02st8i.jpg'
        },
        {
          name: '',
          url: '',
          des: '',
          avatar: 'http://ofyaji162.bkt.clouddn.com/nightcat.jpg'
        },
        {
          name: '',
          url: '',
          des: ', ',
          avatar: 'http://ofyaji162.bkt.clouddn.com/can.jpg'
        },
        {
          name: '_',
          url: 'https://yinmazuo.github.io',
          des: '',
          avatar: 'http://ofyaji162.bkt.clouddn.com/yin.jpg'
        }
      ]
    }
  },
  computed: {
  },
  components: {
    NavBar
  }
}
</script>

<style lang="scss">
.friends {
  min-height: 1000px;
  background-image: url('http://ofyaji162.bkt.clouddn.com/bg1.jpg');
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  overflow-x: hidden;

  .title {
    margin: 50px;
    font-size: 20px;
    color: rgba(255, 255, 255, 0.9);
  }

  .f-list {
    position: relative;
    height: auto;
    margin: auto;
  }

  .f-item {
    position: relative;
    width: 20%;
    height: 300px;
    border-radius: 50%;
    display: inline-block;

    .avatar {
      position: absolute;
      top: 0;
      left: 50%;
      margin-left: -60px;
      display: block;
      width: 120px;
      height: 120px;
      border-radius: 50%;
      background-repeat: no-repeat;
      background-size: cover;
      background-color: rgba(255, 255, 255, 0.2);
      z-index: 9;
    }

    .line {
      position: absolute;
      top: 115px;
      left: 50%;
      margin-left: -10px;
      width: 10px;
      height: 90px;
      border-radius: 30%;
      border-right: 2px solid rgba(128, 64, 0, 0.9);
      z-index: 1;
      cursor: pointer;
    }

    .des {
      position: absolute;
      top: 198px;
      left: 50%;
      margin-left: -90px;
      width: 180px;
      background-color: white;
      color: black;
      border-radius: 10px;
      padding: 5px;
      font-size: 14px;
      backface-visibility: hidden;
      z-index: 2;

      .name {
        font-size: 16px;
        text-align: center;
        line-height: 30px;
      }
    }
  }
}

@media screen and (max-width: 1400px) {
  .friends .f-item {
    width: 25%;
  }
}

@media screen and (max-width: 1100px) {
  .friends .f-item {
    width: 33%;
  }
}

@media screen and (max-width: 670px) {
  .friends .f-item {
    width: 50%;
    .des {
      width: 130px;
      margin-left: -70px;
    }
  }
}
</style>



